.scroll-to-top {
    position: fixed;
    bottom: 15px;
    right: 10px;
  
    width: 30px;
    height: 30px;
    padding-top: 3px;
  
    fill: $white;
    border-radius: 50%;
    border: 0;
    background-color: $orange-btn;
  
    cursor: pointer;
    opacity: 0.5;
    visibility: visible;
    transition: opacity linear 350ms, visibility linear 350ms;
  
    @media screen and (min-width: 768px) {
      bottom: 25px;
      right: 20px;
    }
  }
  
  .scroll-to-top:hover {
    opacity: 0.85;
  }
  
  .scroll-to-top--hidden {
    opacity: 0;
    visibility: hidden;
  }