.pagination-container {
    display: flex;
    justify-content: center;
  
    padding: 40px 0px;
  
    @media screen and (min-width: 768px) {
      padding: 60px 0px;
    }
  }
  
  .pagination-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  
    width: 40px;
    height: 40px;
  
    background-color: $white;
    padding: 5px;
    border-radius: 5px;
  
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 12px;
    line-height: 1.3;
  
    color: $black;
  
    transition: 250ms ease color, 250ms ease background-color;
  
    &:hover,
    &:focus {
      cursor: pointer;
  
      background-color: $orange;
      color: $white;
    }
  
    &.current {
      background-color: $orange;
      color: $white;
    }
  
    &.disabled {
      background-color: $grey-light;
      color: $grey;
  
      cursor: default;
    }
  }
  
  .pagination-btn + .pagination-btn {
    margin-left: 5px;
  }