html {
    position: relative;
    min-height: 100%;
  }
  
  body {
    /* Margin bottom by footer height */
    font-family: 'Roboto', sans-serif;
    margin-bottom: 100px;
    @media screen and (min-width: $tablet) {
      margin-bottom: 79px;
    }
    @media screen and (min-width: $desktop) {
      margin-bottom: 77px;
    }
  }
  
  p,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  li {
    margin: 0px;
    padding: 0px;
  }
  
  a {
    outline: none;
    display: inline-block;
    color: inherit;
    text-decoration: none;
    transition-duration: 250ms;
    transition-timing-function: ease;
  }
  
  button {
    padding: 0;
    border: 0;
    outline: 0;
    margin: 0;
    background-color: transparent;
    font-family: inherit;
    color: inherit;
    fill: currentColor;
    cursor: pointer;
    transition-duration: 250ms;
    transition-timing-function: ease;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  svg {
    color: inherit;
  }
  
  ul {
    padding: 0px;
    margin: 0px;
    list-style: none;
  }
  
  .is-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    border: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
    pointer-events: none;
  }